.stats-section {
  margin-bottom: 2rem;
}
.stats-section .stats-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}
.stats-section .stats-cards .stat-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  transition: all 0.2s ease;
  height: 150px;
}
.stats-section .stats-cards .stat-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: translateY(-1px);
}
.stats-section .stats-cards .stat-card .icon {
  margin-bottom: 0.75rem;
  display: block;
}
.stats-section .stats-cards .stat-card .icon.blue {
  color: #2563eb;
}
.stats-section .stats-cards .stat-card .icon.red {
  color: #dc2626;
}
.stats-section .stats-cards .stat-card .icon.green {
  color: #16a34a;
}
.stats-section .stats-cards .stat-card .number.blue {
  color: #2563eb;
}
.stats-section .stats-cards .stat-card .number.red {
  color: #dc2626;
}
.stats-section .stats-cards .stat-card .number.green {
  color: #16a34a;
}
.stats-section .stats-cards .stat-card .label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #64748b;
  margin: 0;
}
.stats-section .create-button {
  background: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 0.875rem 1.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.stats-section .create-button:hover {
  background: #1d4ed8;
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.stats-section .create-button:active {
  transform: translateY(0);
}
.stats-section .create-button .icon {
  font-size: 0.875rem;
}

@media (max-width: 1024px) {
  .stats-section .stats-cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
@media (max-width: 768px) {
  .stats-section .stats-cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .stats-section .stats-cards .stat-card {
    height: 120px;
    padding: 1rem;
  }
  .stats-section .stats-cards .stat-card .number {
    font-size: 1.5rem;
  }
  .stats-section .stats-cards .stat-card .icon {
    font-size: 1.25rem;
  }
}
